import { useEffect, useReducer } from "react"
import Header from "./Header"
import Main from "./Main"
import Loader from "./Loader"
import Error from "./Error"
import Ready from "./Ready"
import Question from "./Question"
import NextButton from "./NextButton"
import { status } from "server/reply"
import { type } from "@testing-library/user-event/dist/type"
import Progress from "./Progress"
import Finished from "./Finished"
import Timer from "./Timer"

const initialState = { question: [], status: 'Loading', index: 0, answer: null, points: 0, seconds: null }
const SECS_PER_QUESTION = 30

function reducer(state, action) {
  switch (action.type) {
    case "dataReceived":
      return { ...state, question: action.payload, status: "ready" }

    case "dataFailed":
      return { ...state, status: "error" }

    case "start":
      return { ...state, status: "active", seconds: state.question.length * SECS_PER_QUESTION }

    case "newAnswer":
      const question = state.question.at(state.index)
      return {
        ...state, answer: action.payload,
        points:
          action.payload === question.correctOption
            ? state.points + question.points
            : state.points,
      }
    case "nextQuestion":
      return { ...state, index: state.index + 1, answer: null }

    case "Finish":
      return { ...state, status: "finished" }
    case "restart":
      return { ...initialState, question: state.question, status: "ready" }
    case "tick":
      return { ...state, seconds: state.seconds - 1, status: state.seconds === 0 ? "finished" : state.status }
    default:
      throw new Error("Action unknow")
  }
}

export default function App() {
  const [{ question, status, index, answer, points, seconds }, dispatch] = useReducer(reducer, initialState)

  const numQuestions = question.length
  const totalPoints = question.reduce((prev, cur) => prev + cur.points, 0)

  useEffect(() => {
    async function quiz() {
      try {
        const res = await fetch("http://localhost:8000/questions");
        const data = await res.json();
        dispatch({ type: "dataReceived", payload: data });
      } catch (err) {
        dispatch({ type: "dataFailed" })
      }
    }

    quiz(); // Call the async function
  }, []);



  return (<div className="app">
    <Header />
    <Main>
      {status === "Loading" && <Loader />}
      {status === "error" && <Error />}
      {status === "ready" && <Ready numQuestions={numQuestions} dispatch={dispatch} />}
      {status === "active" && (
        <>
          <Progress index={index} numQuestion={numQuestions} points={points} totalPoints={totalPoints} answer={answer} />
          <Question
            question={question[index]}
            dispatch={dispatch}
            answer={answer}
          />
          <Timer dispatch={dispatch} seconds={seconds} />
          <NextButton dispatch={dispatch} answer={answer} index={index} numQuestion={numQuestions} />
        </>)}
      {status === "finished" && <Finished points={points} totalpoints={totalPoints} dispatch={dispatch} />}
    </Main>
  </div>
  )
}